</template>

<script>
import PublicPage from '@/base/PublicPage'

export default {
  components: { PublicPage },
  data: () => ({
    folder: null,
  }),
  computed: {
    parentName() {
      if (!this.folder || !this.folder.parent || !this.folder.parent.url) return 'Home Page'
      return this.folder.parent.name
    },
    parentURL() {
      if (!this.folder || !this.folder.parent || !this.folder.parent.url) return '/'
      return this.folder.parent.url
    },
  },
  created() {
    this.loadFolder()
  },
  watch: {
    $route: 'loadFolder',
  },
  methods: {
    async loadFolder() {
      const path = this.$route.params.rest ? `${this.$route.params.path}/${this.$route.params.rest}` : this.$route.params.path
      this.folder = (await this.$axios.get("/api/folders/", { params: { path } })).data
      if (typeof this.folder === 'string') {
        location.href = this.folder
        return
      }
      if (!this.folder.children) this.folder.children = []
      if (!this.folder.documents) this.folder.documents = []
    },
  }
}
</script>

